import React from 'react'
import { Link, graphql } from 'gatsby'

import Layout from '../components/layout'
import Image from '../components/image'
import Img from "gatsby-image"
import SEO from '../components/seo'

const IndexPage = ({data}) => (
  <Layout>
    <SEO
      title="Trang chủ"
      description="Đây là những sản phẩm do mình làm trong thời gian rảnh, phần lớn là cho người thân và bạn bè. Mục đích chính là để xả stress và sau đó là để phát triển một số sản phẩm của cá nhân Các bạn có chung sở thích cùng mình đều có thể tham gia!"
      keywords={['nghệ thuật chữ viết', 'luật', 'chữ đối xứng', 'chữ tượng hình', 'logo']}
    />
    <h1>Sản phẩm</h1>
    <p>Đây là những sản phẩm do mình làm trong thời gian rảnh, phần lớn là cho người thân và bạn bè. Mục đích chính là
      để xả stress và sau đó là để phát triển một số sản phẩm của cá nhân Các bạn có chung sở thích cùng mình đều có thể
      tham gia!</p>
    <div>
      {data.allNodeProductDetail.edges.map(({node}) => (
        <div>
          <Img
            fixed={node.relationships.field_image.localFile.childImageSharp.fixed
            }
          />
          <h3><Link to={node.fields.slug}>{node.title}</Link></h3>
        </div>
      ))}
    </div>
  </Layout>
)

export default IndexPage

export const query = graphql`
  query {
    allNodeProductDetail(limit: 1000) {
      edges {
        node {
          title
          fields {
            slug
          }
          relationships {
            field_image {
              url
              localFile {
                childImageSharp {
                  fixed(width: 200) {
                    ...GatsbyImageSharpFixed
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`